<template>
  <div class="cjwt_wrap" :class="show === true ? 'hidden' : ''">
    <headers :title="title"></headers>
    <div class="grzl">
      <van-cell-group >
        <van-uploader class="head">
          <span>头像</span><img src="../assets/grzl/head.png" alt="">
        </van-uploader>
        <van-cell id="van-cell" title="昵称" is-link value="郭靖"></van-cell>
        <van-cell id="van-cell" title="卡号"  value="10208888"></van-cell>
        <van-cell id="van-cell" title="姓名" is-link value="郭靖"></van-cell>
        <van-cell id="van-cell" title="性别" is-link value="男"></van-cell>
        <van-cell @click="status" id="van-cell" title="地区" is-link v-model="address"></van-cell>
        <van-cell id="van-cell" title="个性签名" is-link ></van-cell>
      </van-cell-group>
       <p id="onwer">以下信息为非公开信息</p>
       <van-cell-group >
        <van-cell id="van-cell" title="身份证号"  value="5112211983*******"></van-cell>
        <van-cell id="van-cell" title="手机号" is-link value="10208888"></van-cell>
        <van-cell id="van-cell" title="邮箱" is-link ></van-cell>
        <van-cell id="van-cell" title="QQ号码" is-link></van-cell>
        <van-cell id="van-cell" title="12306账号" is-link ></van-cell>
      </van-cell-group>
      <div class="bottom"></div>
      <van-cell-group>
        <van-cell id="van-cell" title="密码管理" is-link ></van-cell>
        <van-cell id="van-cell" title="微信绑定" is-link ></van-cell>
      </van-cell-group>
      <div class="bottom"></div>
      <p class="logout">退出登录</p>
    </div>
    <van-area v-if="show" :areaList="areaList" @cancel="status" @confirm="confirm"/>
  </div>
</template>
<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  import jarea from '../mock/area.json'
  export default {
    data() {
      return {
        title: "个人资料",
        areaList: jarea,
        show: false,
        address: '重庆市'
      };
    },
    methods: {
      status() {
        this.show = !this.show
      },
      confirm(data) {
        this.address = data[0].name + data[1].name + data[2].name;
        this.show = !this.show;
      }
    },
    components: {
      headers
    }
  };
</script>
<style scoped>
  .cjwt_wrap {
    flex: 1;
    height: auto;
    background: #F8F8FF;
  }

  .hidden{
    overflow: hidden;
  }

  .head {
    display: flex;
    font-size: 32px;
    justify-content: space-between;
    height: 180px;
    align-items: center;
    background: url('../assets/grzl/travel_search_icon_arrow_right.png') no-repeat center right 30px;
    background-size: 18px 30px;
    border-bottom: 1px solid #d4d4d6;
  }

  img {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    margin-right: 76px;
  }

  .grzl {
    margin-top: 110px;
    padding-bottom: 40px;
    background: #f6f6f6;
  }

  #van-cell {
    font-size: 32px;
    color: #000;
  }

  #onwer {
    font-size: 24px;
    background: #eee;
    color: #818389;
    height: 80px;
    line-height: 80px;
    padding-left: 30px;
  }

  .logout {
    font-size: 32px;
    color: #000;
    text-align: center;
    height: 96px;
    line-height: 96px;
    background: #fff url('../assets/grzl/travel_search_icon_arrow_right.png') no-repeat center right 30px;
    background-size: 18px 30px;
  }

  .bottom {
    height: 40px;
    background: #f6f6f6;
  }

  .van-area {
    position: absolute;
    bottom: 0;
    width: 100%;
  }
</style>



